@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap);
.general-wrapper{margin:2rem;padding-bottom:2rem;border-bottom:#333333 1px solid}.general-wrapper .title{font-size:3rem;font-weight:400;color:#333333}.general-wrapper .general-form{font-size:1.8rem;display:flex;flex-direction:column}.general-wrapper .general-form input{margin:0.5rem 0 0.5rem}

.education-field-wrapper{margin:0 1rem 0 1rem;padding-bottom:2rem}.education-field-wrapper .education-form{font-size:1.8rem;display:flex;flex-direction:column}.education-field-wrapper .education-form input{margin:0.5rem 0 0.5rem}.education-field-wrapper+.education-field-wrapper{padding-top:2rem;border-top:#333333 1px solid}

.education-wrapper{margin:2rem;padding-bottom:2rem;border-bottom:#333333 1px solid}.education-wrapper .title-wrapper{display:flex;align-items:center}.education-wrapper .title-wrapper .title{font-size:3rem;font-weight:400;color:#333333}.education-wrapper .title-wrapper .add-education{font-size:2.5rem;margin-left:2rem;color:#3ec1d3;cursor:pointer;opacity:.3}.education-wrapper .title-wrapper .add-education:hover{opacity:1}.education-wrapper .title-wrapper .remove-education{font-size:2.5rem;margin-left:2rem;color:#ff9a00;cursor:pointer;opacity:.3}.education-wrapper .title-wrapper .remove-education:hover{opacity:1}

.experience-field-wrapper{margin:0 1rem 0 1rem;padding-bottom:2rem}.experience-field-wrapper .experience-form{font-size:1.8rem;display:flex;flex-direction:column}.experience-field-wrapper .experience-form input{margin:0.5rem 0 0.5rem}.experience-field-wrapper+.experience-field-wrapper{padding-top:2rem;border-top:#333333 1px solid}

.experience-wrapper{margin:2rem;padding-bottom:2rem;border-bottom:#333333 1px solid}.experience-wrapper .title-wrapper{display:flex;align-items:center}.experience-wrapper .title-wrapper .title{font-size:3rem;font-weight:400;color:#333333}.experience-wrapper .title-wrapper .add-experience{font-size:2.5rem;margin-left:2rem;color:#3ec1d3;cursor:pointer;opacity:.3}.experience-wrapper .title-wrapper .add-experience:hover{opacity:1}.experience-wrapper .title-wrapper .remove-experience{font-size:2.5rem;margin-left:2rem;color:#ff9a00;cursor:pointer;opacity:.3}.experience-wrapper .title-wrapper .remove-experience:hover{opacity:1}

.display-general-wrapper{margin-left:10rem;grid-column:1/2;grid-row:1/2;align-self:center}.display-general-wrapper h2{font-size:3rem;font-weight:400}.display-general-wrapper h3{font-size:2rem;font-weight:300}

.picture-wrapper{grid-column:2/3;grid-row:1/2;align-self:center;justify-self:right;margin-right:10rem}.picture-wrapper .picture{width:20rem;border-radius:1rem}

.display-education-field-wrapper{background-color:#aaaaaa;border-radius:1rem;color:#f4f4f4;display:flex;flex-direction:column;justify-content:center}.display-education-field-wrapper h3{font-size:1.5rem;margin-left:1rem}.display-education-field-wrapper+.display-education-field-wrapper{margin-top:2rem}

.display-education-wrapper{margin-left:10rem;margin-right:2rem;width:calc(90%-10rem);grid-column:1/2;grid-row:2/3}.display-education-wrapper:last-child{margin-bottom:2rem}.display-education-wrapper h2{font-size:3rem;font-weight:400}

.display-experience-field-wrapper{background-color:#aaaaaa;border-radius:1rem;color:#f4f4f4;display:flex;flex-direction:column;justify-content:center;margin-right:1rem}.display-experience-field-wrapper h3{font-size:1.5rem;margin-left:1rem}.display-experience-field-wrapper+.display-experience-field-wrapper{margin-top:2rem}

.display-experience-wrapper{margin-left:10rem;margin-right:2rem;width:calc(90%-12rem);grid-column:2/3;grid-row:2/3}.display-experience-wrapper:last-child{margin-bottom:2rem}.display-experience-wrapper h2{font-size:3rem;font-weight:400}

.container{width:100vw;height:100vh;display:grid;align-items:center;justify-items:center;grid-template-columns:1fr 2fr}.container .input-section{width:90%;height:90%;background-color:#eeeeee;border-radius:1rem;overflow-y:auto}.container .display-section{width:90%;height:90%;background-color:#eeeeee;border-radius:1rem;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr}

*{font-size:62.5%;font-family:'Montserrat', sans-serif}body{margin:0;padding:0;box-sizing:border-box}

